html {
  font-size: 62.5%; /* 1rem = 10px on default browser settings */
}

html,
body {
  height: 100%;
}

body {
  font-family: 'Lucida Grande', Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
}

/* ensure tag works in IE 9-11 */
main {
  display: block;
}

.text-center {
  text-align: center;
}

.block-center {
  margin: 0 auto;
}

/* github-corners */
.github-corner > svg {
  opacity: 0.5;
  -webkit-transition: opacity 0.4s;
  -o-transition: opacity 0.4s;
  transition: opacity 0.4s;
}

.github-corner:hover > svg {
  opacity: 1;
}



